import React, { useState } from "react";
import ProfileImageWithDefault from "./ProfileImageWithDefault";
import { Link } from "react-router-dom";
import { format } from "timeago.js";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { deleteHoax } from "../api/apiCalls";
import Modal from "./Modal";
import { useApiProgress } from "../shared/ApiProgress";

const HoaxView = (props) => {
  const loggedInUser = useSelector((store) => store.username);
  const { hoax, onDeleteHoax } = props;
  const { user, content, timestamp, fileAttachment, id } = hoax;
  const { username, displayName, image } = user;
  const [modalVisible, setModalVisible] = useState(false);
  const { t } = useTranslation();
  const { i18n } = useTranslation();
  const formatted = format(timestamp, i18n.language);

  const pendingApiCall = useApiProgress(
    "delete",
    `/api/1.0/hoaxes/${id}`,
    true
  );

  const ownedByLoggedInUser = loggedInUser === username;

  const onClickDelete = async () => {
    await deleteHoax(id);
    onDeleteHoax(id);
  };

  const onClickCancel = () => {
    setModalVisible(false);
  };

  return (
    <>
      <div className="card p-1">
        <div className="d-flex">
          <ProfileImageWithDefault
            image={image}
            height="32"
            width="32"
            className="rounded-circle m-1"
          />
          <div className="flex-fill m-auto ps-2">
            <Link to={`/user/${username}`} className="text-dark">
              <h6 className="d-inline">
                {displayName}@{username}
              </h6>
            </Link>
            <span> - </span>
            <span>{formatted}</span>
          </div>
          {ownedByLoggedInUser && (
            <button
              className="btn btn-delete-link btn-sm"
              onClick={() => setModalVisible(true)}
            >
              <i className="material-icons">delete_outline</i>
            </button>
          )}
        </div>
        <div className="ps-5">{content}</div>
        {fileAttachment && (
          <div className="ps-5">
            <img
              className="img-fluid"
              src={"images/attachments/" + fileAttachment.name}
              alt={content}
            />
          </div>
        )}
      </div>
      <Modal
        visible={modalVisible}
        title={t("Delete Hoax")}
        onClickCancel={onClickCancel}
        onClickOk={onClickDelete}
        message={
          <div>
            <div>
              <strong>{t("Are you sure to delete hoax?")}</strong>
            </div>
            <span>{content}</span>
          </div>
        }
        pendingApiCall={pendingApiCall}
        okButton={t("Delete Hoax")}
      />
    </>
  );
};

export default HoaxView;
